* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: rgb(0, 0, 0);
  --color-bg-variant: rgb(100, 100, 100);
  --color-primary: #c7c7c7;
  --color-primary-variant: rgba(255, 255, 255, 0.4);
  --color-white: #ffffff;
  --color-light: rgba(51, 51, 51, 0.6);
  --transition: all 400ms ease;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Verdana", sans-serif;
  background: var(--color-bg-variant);
  color: var(--color-white);
}

.container {
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

section {
  margin-top: 4rem;
}

section > h5 {
  text-align: center;
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}
